// Types
import types from './types';

export const postsActions = Object.freeze({
    clearPosts: () => ({
        type: types.CLEAR_POSTS,
    }),
    fetchPosts: () => ({
        type: types.FETCH_POSTS,
    }),
    fetchPostsSuccess: (posts) => ({
        type:    types.FETCH_POSTS_SUCCESS,
        payload: posts,
    }),
    fetchPostsFail: (error) => ({
        type:    types.FETCH_POSTS_FAIL,
        payload: error,
        error:   true,
    }),
    createPost: (comment) => ({
        type:    types.CREATE_POST,
        payload: comment,
    }),
    createPostSuccess: (post) => ({
        type:    types.CREATE_POST_SUCCESS,
        payload: post,
    }),
    createPostFail: (error) => ({
        type:    types.CREATE_POST_FAIL,
        payload: error,
        error:   true,
    }),
    removePost: (id) => ({
        type:    types.REMOVE_POST,
        payload: id,
    }),
    removePostSuccess: (id) => ({
        type:    types.REMOVE_POST_SUCCESS,
        payload: id,
    }),
    removePostFail: (error) => ({
        type:    types.REMOVE_POST_FAIL,
        payload: error,
        error:   true,
    }),
    likePost: (postID) => ({
        type:    types.LIKE_POST,
        payload: postID,
    }),
    likePostSuccess: (likedPostIds) => ({
        type:    types.LIKE_POST_SUCCESS,
        payload: likedPostIds,
    }),
    likePostFail: (error) => ({
        type:    types.LIKE_POST_FAIL,
        payload: error,
        error:   true,
    }),
    unlikePost: (postID) => ({
        type:    types.UNLIKE_POST,
        payload: postID,
    }),
    unlikePostSuccess: (unlikedPostID) => ({
        type:    types.UNLIKE_POST_SUCCESS,
        payload: unlikedPostID,
    }),
    unlikePostFail: (error) => ({
        type:    types.UNLIKE_POST_FAIL,
        payload: error,
        error:   true,
    }),
});
