// Core
import { call, put, select } from 'redux-saga/effects';
import { replace } from 'react-router-redux';
import { actions } from 'react-redux-form';

// Instruments
import { api } from 'config';
import { book } from 'navigation/book';
import { uiActions } from 'bus/ui/actions';
import { authActions } from 'bus/auth/actions';
import { profileActions } from 'bus/profile/actions';
import { postsActions } from 'bus/posts/actions';
import { usersActions } from 'bus/users/actions';

export function* callLogoutWorker () {
    try {
        yield put(uiActions.setAuthFetchingState(true));

        const token = yield select((state) => state.profile.get('token'));

        const response = yield call(fetch, `${api}/user/logout`, {
            method:  'GET',
            headers: {
                Authorization: token,
            },
        });

        if (response.status !== 204) {
            const { message } = yield call([response, response.json]);

            throw new Error(message);
        }

        yield put(authActions.logoutSuccess());
    } catch (error) {
        yield put(authActions.logoutFail(error));
    } finally {
        yield localStorage.removeItem('token');
        yield put(replace(book.login));
        yield put(profileActions.clearProfile());
        yield put(postsActions.clearPosts());
        yield put(usersActions.clearUsers());
        yield put(actions.reset('forms.user'));
        yield put(uiActions.setAuthFetchingState(false));
    }
}
