// Core
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// Instruments
import { postsActions } from 'bus/posts/actions';
import { usersActions } from 'bus/users/actions';

// Components
import { Notifications, Spinner, Navigation, Catcher, Posts } from 'components';

const mapStateToProps = (state) => {
    return {
        isFeedFetching: state.ui.get('isFeedFetching'),
        profile:        state.profile,
        posts:          state.posts,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        actions: bindActionCreators(
            { ...postsActions, ...usersActions },
            dispatch
        ),
    };
};

@connect(mapStateToProps, mapDispatchToProps)
export default class Feed extends Component {
    render () {
        const { actions, isFeedFetching, profile, posts } = this.props;

        return (
            <>
                <Notifications />
                <Spinner isSpinning = { isFeedFetching } />
                <Navigation />
                <Catcher>
                    <Posts actions = { actions } posts = { posts } profile = { profile } />
                </Catcher>
            </>
        );
    }
}
