// Core
import { call, put } from 'redux-saga/effects';
import { actions } from 'react-redux-form';

// Instruments
import { api } from 'config';
import { uiActions } from 'bus/ui/actions';
import { authActions } from 'bus/auth/actions';
import { profileActions } from 'bus/profile/actions';

export function* callLoginWorker ({ payload: user }) {
    try {
        yield put(uiActions.setAuthFetchingState(true));

        const response = yield call(fetch, `${api}/user/login`, {
            method:  'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(user),
        });

        const { data: profile, message } = yield call([
            response,
            response.json
        ]);

        if (response.status !== 200) {
            throw new Error(message);
        }

        if (user.remember) {
            yield localStorage.setItem('token', profile.token);
        }

        yield put(authActions.loginSuccess());
        yield put(profileActions.fillProfile(profile));
        yield put(
            actions.change('forms.user.profile.firstName', profile.firstName)
        );
        yield put(
            actions.change('forms.user.profile.lastName', profile.lastName)
        );
        yield put(actions.reset('forms.login'));
    } catch (error) {
        yield put(authActions.loginFail(error));
    } finally {
        yield put(uiActions.setAuthFetchingState(false));
    }
}
