// Core
import { takeEvery } from 'redux-saga/effects';

// Instruments
import types from 'bus/profile/types';
import { callUpdateProfileWorker } from './workers/updateProfile';
import { callUpdateAvatarWorker } from './workers/updateAvatar';

export const profileWatchers = Object.freeze({
    * watchUpdateProfile () {
        yield takeEvery(types.UPDATE_PROFILE, callUpdateProfileWorker);
    },
    * watchUpdateAvatar () {
        yield takeEvery(types.UPDATE_AVATAR, callUpdateAvatarWorker);
    },
});
